/** RECORDEMOS QUE EN LA FUNCUON DE EVENTLISTENERS() ES DONDE SE AGREGARAN LOS EVENTOS POR CLICK ASEREJE */
document.addEventListener('DOMContentLoaded', function(){
    if(document.location.pathname.startsWith('/admin')){
      sidebarMenu();

    }
    //tooltips de enlaces
    var tooltipTriggerList = [].slice.call(document.querySelectorAll('[data-bs-toggle="tooltip"]'));
    var tooltipList = tooltipTriggerList.map(function (tooltipTriggerEl) {
        return new bootstrap.Tooltip(tooltipTriggerEl);
    });
    const element = document.querySelector('.navegacion-up');
    const element2 = document.querySelector('.full-icono');
  // popovers
    var popoverTriggerList = [].slice.call(document.querySelectorAll('[data-bs-toggle="popover"]'));
    var popoverList = popoverTriggerList.map(function (popoverTriggerEl) {
      return new bootstrap.Popover(popoverTriggerEl);
    })
    var popover = new bootstrap.Popover(document.querySelector('.popover-dismiss'), {
      trigger: 'focus'
    })

    createPopper(reference, popper, {
        modifiers: [
            {
            name: 'flip',
            options: {
                boundary: element,
                boundary: element2,
            },
            },
        ],
    });
});


function sidebarMenu(){
    
  //Colapsando Sidebar
  const SideIcono = document.querySelector('.full-icono');
  const sidebar = document.querySelector('.contenedor-side');
  const sidebarSeccion = document.querySelector('.body-admin');
  const prueba = document.querySelectorAll('.menu-side-grande');
  const iconoColl = document.querySelectorAll('.icono-coll');

  //evento para colapsar o abrir sidebar
  SideIcono.addEventListener('click', function(){
      // verificando si esta disponible en la vista el sidebar
      if(sidebar.classList.contains('disponible')){
          //esta disponible el sidebar, eliminar la etiqueta de los side y agregar el de los iconos
          sidebar.classList.remove('disponible');
          for(var i = 0; i<prueba.length; i++){
              prueba[i].classList.remove('mostrar-side');
              //agregando la etiqueta de dislay-none para ocultarla
              prueba[i].classList.add('d-none');
          };
          for(var l = 0; l<iconoColl.length; l++){
              iconoColl[l].classList.add('mostrar-icono');
          } 
          //modificando el grid de bodyadmin
          sidebarSeccion.classList.add('modificar');
      }else{
          //estan disponibles los iconos, eliminar etiqueta de mostrar y agregarla en el sidebar
          sidebar.classList.add('disponible');
          for(var i = 0; i<prueba.length; i++){
              prueba[i].classList.add('mostrar-side');

          };
          for(var l = 0; l<iconoColl.length; l++){
              iconoColl[l].classList.remove('mostrar-icono');
          } 
          //regresando el grid original de body admin
          sidebarSeccion.classList.remove('modificar');

      }
  });

};

//funcion para expandir pantalla
function toggleFullScreen(elem) {
    if ((document.fullScreenElement !== undefined && document.fullScreenElement === null) || (document.msFullscreenElement !== undefined && document.msFullscreenElement === null) || (document.mozFullScreen !== undefined && !document.mozFullScreen) || (document.webkitIsFullScreen !== undefined && !document.webkitIsFullScreen)) {
      if (elem.requestFullScreen) {
        elem.requestFullScreen();
      } else if (elem.mozRequestFullScreen) {
        elem.mozRequestFullScreen();
      } else if (elem.webkitRequestFullScreen) {
        elem.webkitRequestFullScreen(Element.ALLOW_KEYBOARD_INPUT);
      } else if (elem.msRequestFullscreen) {
        elem.msRequestFullscreen();
      }
    } else {
      if (document.cancelFullScreen) {
        document.cancelFullScreen();
      } else if (document.mozCancelFullScreen) {
        document.mozCancelFullScreen();
      } else if (document.webkitCancelFullScreen) {
        document.webkitCancelFullScreen();
      } else if (document.msExitFullscreen) {
        document.msExitFullscreen();
      }
    }
}



