
// ARCHIVO JAVASCRIPT PARA CRUD TAREAS Y AJAX //
document.addEventListener('DOMContentLoaded', function(){

    // LLAMANDO EVENTOS //
    eventListenersTareas();

});

function eventListenersTareas() {
    console.log(window.location.pathname);
    var url = window.location.pathname;
    if(url === '/admin/area-admin'){
        document.getElementById('tareas-index').addEventListener('click', cambiarTareas);
        document.getElementById('agregar-tarea-contenedor-index').addEventListener('click', agregarTarea);
        mostrarTareasEstado();
        checkTarea();

    }else if(url === '/admin/tareas'){
        checkTarea();
        ordenarTareas();

    }

    agregarTareaSide();

      

   
}
// MOSTRANDO TAREAS INDEX RESPECTO AL ESTADO //
function mostrarTareasEstado() {
    var divContenedor = document.getElementById('tareas-index');
    progreso = document.getElementById('btn-progreso');
    completadas = document.getElementById('btn-completadas');
    tareas = document.querySelectorAll('#tarea-div');
    // MOSTRANDO LAS TAREAS DEPENDIENDO DE SU ESTADO //
    for(let i = 0; i < tareas.length; i++){
        var estado = tareas[i].getAttribute('estado');
        if(progreso.classList.contains('activo') && completadas.classList.contains('no-activo')){
            switch (estado) {
                case '0':
                    tareas[i].style.display = 'flex'
                    break;
                case '1':
                    tareas[i].style.display = 'none'
                    break;
            
                default:
                    break;
            }

        }else if(progreso.classList.contains('no-activo') && completadas.classList.contains('activo')){
            switch (estado) {
                case '0':
                    tareas[i].style.display = 'none'
                    break;
                case '1':
                    tareas[i].style.display = 'flex'
                    break;
            
                default:
                    break;
            }
        }
    }

    // console.log(tareas);
}
// CAMBIANDO TAREAS DEL CONTENEDOR DE INDEX //
function cambiarTareas() {
    var divContenedor = document.getElementById('tareas-index');
        progreso = document.getElementById('btn-progreso');
        completadas = document.getElementById('btn-completadas');
        tareas = document.querySelectorAll('#tarea-div');
    // CAMBIANDO CLASES DE ACTIVO Y NO ACTIVO//
    if(progreso.classList.contains('activo') && completadas.classList.contains('no-activo')){
        // BOTON DE PROGRESO //
        progreso.classList.remove('activo');
        progreso.classList.add('no-activo');
        // BOTON DE TAREA COMPLETADAS //
        completadas.classList.add('activo');
        completadas.classList.remove('no-activo');

    }else if(progreso.classList.contains('no-activo') && completadas.classList.contains('activo')){
        // BOTON DE PROGRESO //
        progreso.classList.add('activo');
        progreso.classList.remove('no-activo');
        // BOTON DE TAREA COMPLETADAS //
        completadas.classList.remove('activo');
        completadas.classList.add('no-activo');
    }
    
    mostrarTareasEstado();

   
    
}
// FUNCION QUE ESTA ATENTO AL CLICK EN EL SIDEBAR PARA AGREGAR TAREA //
function agregarTareaSide(){
    var enlace = document.querySelectorAll('#agregar-tarea');
    for(let i = 0; i < enlace.length; i++){
        enlace[i].addEventListener('click', agregarTarea);
    }
}
// FUNCION ATENTO AL CHECKBOX DE LA TAREA //
function checkTarea(){
    var inputs = document.querySelectorAll('#check-tarea');
    for(let i = 0; i < inputs.length; i++){
        inputs[i].addEventListener('change', editarEstadoTarea);
    }

}
// FUNCION QUE EDITA EL ESTADO DE LA TAREA //
function editarEstadoTarea(e){
    var divPadre = e.target.parentElement.parentElement;
        id_tarea = e.target.value;
        estado = divPadre.getAttribute('estado');
        
        // LEYENDO ESTADO Y ENVIANDO EL VALOR CORRECTO A LA BD //
        if(estado === '0'){
            estado_n = '1';
        }else if(estado === '1'){
            estado_n = '0';
        }

    // CAMBIANDO ESTADO EN BASE DE DATOS //
    (async () =>{
        // DATOS QUE SE ENVIAN AL SERVIDOR //
        var datos = new FormData();
        datos.append('id', id_tarea);
        datos.append('completado', estado_n);

        // LLAMADO AJAX //
        var xhr = new XMLHttpRequest()

        // ABRIENDO CONEXION //
        xhr.open('POST', '/admin/status-tarea', true);

        // RETORNO DE DATOS //
        xhr.onload = function(){
            if(this.status === 200){
                var respuesta = JSON.parse(xhr.responseText);
                
                if(respuesta.actualizado === true){
                    // CAMBIANDO ESTADO DE LA TAREA EN EL DOM //
                    divPadre.setAttribute('estado', respuesta.tarea['completado']);
                    var url = window.location.pathname;
                    if(url === '/admin/area-admin'){
                        mostrarTareasEstado();
                        // DESELECCIONANDO EL CHECKBOX //
                        e.target.checked = false;
                    }else if(url === '/admin/tareas'){
                        var parrafo = divPadre.children[3].children[0];
                            clases = divPadre.children[3].children[0].classList;
                            // CAMBIANDO LA TABLA DE VALORES //
                            if(clases.contains('pendiente') && respuesta.tarea['completado'] === '1'){
                                if(clases.contains('pendiente')){
                                    clases.remove('pendiente');
                                }
                                clases.add('completado');
                                parrafo.innerHTML = `completado`;
                            }else if(clases.contains('completado') && respuesta.tarea['completado'] === '0'){
                                if(clases.contains('completado')){
                                    clases.remove('completado');
                                }
                                clases.add('pendiente');
                                parrafo.innerHTML = `pendiente`;

                            }
                
                    }
                

                }
                console.log(respuesta)
            }
        }

        // ENVIANDO LA PETICION //
        xhr.send(datos)

    })();


}
// FUNCION QUE AGREGA LA TAREA Y PETICION AJAX //
function agregarTarea() {
    if(document.getElementById('offcanvasSide')){
        document.getElementById('offcanvasSide').style.visibility = 'hidden';
    }
    // MOSTRANDO POPUP PARA AGRERGAR TAREA
    (async () => {
        const { value: formValues} = await Swal.fire({
            allowOutsideClick: false,
            allowEscapeKey: false,
            showCancelButton: true,
            confirmButtonText: 'Agregar',
            confirmButtonColor: '#5b50fa',
            cancelButtonText: 'Cancelar',
            title: '<h1>Agregar nueva tarea!</h1>',
            html:
              '<label for="fecha-tarea" class="swal2-input-label">Fecha Limite para cumplir la tarea</label>'+
              '<input type="date" id="fecha-tarea" name="fecha-tarea" class="tarea-input swal2-input">',
            focusConfirm: false,
            input: 'textarea',
            inputLabel: 'Nueva Tarea',
            inputPlaceholder: 'Escribe tu nueva tarea...',
            inputAttributes: {
              'aria-label': 'Escribe tu nueva tarea',
            },
            customClass: {
                popup: 'agregar-tarea',
                input: 'tarea-input'
            },
            inputAutoTrim: true,
            
            preConfirm: () => {
              return [
                document.getElementById('fecha-tarea').value,
                document.getElementById('swal2-input').value
              ]
            }
        })
          
        if (formValues) {
            var fechaTarea = formValues[0];
                tarea = formValues[1].trim();
            // FECHA DE CREACION //
            var fecha = new Date();;

            // CONVIERTENDO LA FECHA LIMITE Y FECHA CREACION AL FORMATO REQUERIDO //
            if(fechaTarea){
                var nuevaFecha = new Date(fechaTarea.replace(/-/g, '\/'));
                var dia = nuevaFecha.getDate();
                    mes = nuevaFecha.getMonth() + 1;
                    ano = nuevaFecha.getFullYear();
                var limite = (ano+"-"+mes+"-"+dia);
            }
            if(fecha){
                var idk = new Date(fecha);
                var dia2 = idk.getDate();
                    mes2 = idk.getMonth() + 1;
                    ano2 = idk.getFullYear();
                var creado = (ano2+"-"+mes2+"-"+dia2);
            }

            
            // VALIDANDO QUE EL VALOR INTRODUCIDO SEA VALIDO //
            var valoresAceptados = /^[a-zA-Z-\u00f1\u00d1]+((((\s){1})?)[a-zA-Z-\u00f1\u00d1]((([\.\,\\\/]){1})?))*$/ ;
            if(tarea.match(valoresAceptados)){
                // TOMANDO DATOS DE LA SESSION //
                var idAdmin = document.getElementById('id-admin-tarea');
            
                // DATOS QUE SE ENVIAN AL SERVIDOR //
                var datos = new FormData();
                datos.append('tarea', tarea);
                datos.append('id_usuario', idAdmin.value);
                datos.append('fecha_limite', limite);
                datos.append('fecha_creada', creado);
    
                // LLAMADO AJAX //
                var xhr = new XMLHttpRequest();
    
                // ABRIENDO CONEXION //
                xhr.open('POST', '/admin/crear-tarea', true);
    
                // RETORNO DE DATOS //
                xhr.onload = function () {
                    if(this.status === 200){
                        var respuesta = JSON.parse(xhr.responseText);
    
                        // LEYENDO RESPUESTAS//
                        if(respuesta.resultado === true && respuesta.errores === false){
                            // SE GUARDO CORRECTAMENTE EN LA BASE DE DATOS //
                            // TOMANDO VALORES DE LA RESPUESTA //
                            var tareaInsertada = respuesta.tarea['tarea'];
                                id_usario_tarea = respuesta.tarea['id_usuario'];
                                estado_tarea = '0';
                                id_insertado = respuesta.id_insertado;
                                fecha_creada = respuesta.tarea['fecha_creada'];
                                fecha_limite = respuesta.tarea['fecha_limite'];

                            // VERIFICANDO UBICACION DEL ADMINISTRADOR //
                            var url = window.location.pathname;
                            if(url === '/admin/area-admin'){
                                var contenedor = document.getElementById('contenido-tareas');
                                nuevaTarea = document.createElement('div');
                                nuevaTarea.setAttribute('id', 'tarea-div');
                                nuevaTarea.setAttribute('estado', '0');
                                nuevaTarea.classList.add('tarea');
                                nuevaTarea.innerHTML = `
                                    <div class="input-tarea">
                                        <input type="checkbox" id="tareaId" value="${id_insertado}">
                                        <label for="tareaId">${tareaInsertada}</label>
                                    </div>
                                    <div class="acciones-tarea">
                                        <a href="#" class="editar">
                                            <i class="bi bi-pencil"></i>
                                            <input type="hidden" value="${id_insertado}">
                                        </a>
                                        <a href="#" class="eliminar"><i class="bi bi-trash"></i>
                                            <input type="hidden" id="TareaIdGet">
                                        </a>
                                    </div>
                                `;

                                contenedor.appendChild(nuevaTarea);
                                mostrarTareasEstado();
                            }else if(url === '/admin/tareas'){
                                var contenedor = document.getElementById('contenido-tareas');
                                    nuevaTarea = document.createElement('tr');
                                    nuevaTarea.setAttribute('id', 'tarea-tr');
                                    nuevaTarea.setAttribute('estado', '0');
                                    nuevaTarea.classList.add('fila-tarea');
                                    nuevaTarea.innerHTML = `
                                        <td class="checkbox-tarea"><input type="checkbox" id="check-tarea"></td>
                                        <td class="fecha-creada">${fecha_creada}</td>
                                        <td class="tareas"><p class="tarea">${tareaInsertada}</p></td>
                                        <td class="estado-tarea"><p class="status-tarea pendiente">pendiente</p></td>
                                        <td class="fecha-limite">${fecha_limite}</td>
                                        <td class="opciones-tarea">                            
                                            <a href="#" class="editar">
                                                <i class="bi bi-pencil"></i>
                                                <input type="hidden" value="${id_insertado}">
                                            </a>
                                            <a href="#" class="eliminar"><i class="bi bi-trash"></i>
                                                <input type="hidden" id="TareaIdGet">
                                            </a>
                                        </td>
            
                                    `;
                                    contenedor.appendChild(nuevaTarea);


                            }
                        



                                
                            Swal.fire(
                                'Nueva Tarea',
                                'Tarea agendada',
                                'success'
                            )

                        }else if(respuesta.resultado === false && respuesta.errores !== false){
                            Swal.fire({
                                icon: 'error',
                                title: 'Oops...',
                                text: 'No se pudo guardar la tarea, intentalo mas tarde!',
                            })
                        }
    
                        console.log(respuesta);
                    }
                }
                // ENVIAR LA PETICION // 
                xhr.send(datos);

            }else{
                // LOS VALORES INTRODUCIDOS NO SON ACEPTADO
                Swal.fire({
                    icon: 'error',
                    title: 'Oops...',
                    text: 'Parece que los valores introducidos no son validos, revisalos!',
                })
            }

        }
        if(document.getElementById('offcanvasSide')){
            document.getElementById('offcanvasSide').style.visibility = 'visible';
        }
    })();


}

