//  FUNCION PARA VERIFICAR INPUTS //
function InputVerify(formulario){
    if(formulario.getAttribute('id') === 'form-crear-admin' || formulario.getAttribute('id') === 'form-editar-admin'){
        var args = document.querySelectorAll('.input');
        for( let i=0; i < args.length; i++){
            // DETERMINANDO SI EL INPUT ESTA VACIO //
            var inputValor = args[i].value;

            // SI ESTA VACIO MOSTRAR ALERTA EN EL INPUT-DIV //
            if(inputValor === ''){
                // OBTENIENDO VALOR DE ERROR DE INPUT //
                var nota = args[i].getAttribute('data-verify');
                // IMPRIMIENDO ERROR //
                if(args[i].parentElement.lastElementChild.classList.contains('validacion')){
                    var divAnterior = args[i].parentElement.lastElementChild;
                    divAnterior.remove();
                }
                var div = document.createElement("div");
                div.classList.add('validacion');
                div.setAttribute('id', 'validacion');
                div.innerHTML = nota;
                var divInput = args[i].parentElement;
                divInput.appendChild(div);

                // RESALTANDO INPUT //
                validandoInput(args[i]);
            }
        }
    }
}

// INPUTS CONTRASEA HACEN MATCH //
function matchClaves(e){
    var botonSubmit = document.getElementById('boton-submit');
        inputClave = document.getElementById('clave');
        inputRep = document.getElementById('claveRep');
        divPadre = document.getElementById('claveRep').parentElement;
    // CREANDO EL DIV PARA ALERTA DE MATCH //
    if(document.getElementById('match-clave')){
        // SI EXISTE YA EL DIV, SE ELIMINA //
        var divAnterior = document.getElementById('match-clave');
        divAnterior.remove();
    }
    var div = document.createElement("div");
    div.setAttribute('id', 'match-clave');
    div.classList.add('no-match');
    var nota = '';

        inputRep.addEventListener('keyup', function () {
            if(inputClave.value === inputRep.value){
                // ASEGURANDO QUE SE MUESTRE EL DIV CUANDO ESTE TYPING EN EL FORM //
                if(div.classList.contains('d-none')){
                    div.classList.remove('d-none');
                }
                div.classList.remove('no-match');
                div.classList.add('match');
                nota = "Las contraseas concuerdan";
                div.innerHTML = nota;
                if(botonSubmit.classList.contains('disabled')){
                    botonSubmit.classList.remove('disabled');
                }
            }else{
                // ASEGURANDO QUE SE MUESTRE EL DIV CUANDO ESTE TYPING EN EL FORM //
                if(div.classList.contains('d-none')){
                    div.classList.remove('d-none');
                }
                if(div.classList.contains('match')){
                    div.classList.remove('match');
                }
                div.classList.add('no-match');
                nota = "Las contraseas no concuerdan";
                div.innerHTML = nota;
                botonSubmit.classList.add('disabled');
            }
            return  nota;
        });

        divPadre.appendChild(div);

}

// VALIDANDO TIPO DE CONTENIDO INPUTS //
function tipoInput(formulario){
    var arreglo = [];
    for (let i = 0; i < formulario.length; i++) {
        switch (formulario[i].type) {
            case 'text':
                switch (formulario[i].name) {
                    // VERIFICANDO EL NAME DEL INPUT TEXT //
                    case 'nombre':
                        var valoresAceptados = /^[a-zA-Z-\u00f1\u00d1]+((((\s){1})?)[a-zA-Z-\u00f1\u00d1]((([\.\,]){1})?))*$/ ;
                        if(formulario[i].value.match(valoresAceptados)){
                            // console.log(formulario[i].value);
                            continuar = true;
                            arreglo.push(continuar);
                        }else{
                            continuar = false;
                            arreglo.push(continuar);
                        }
    
                        break;
                
                    case 'cargo':
                        var valoresAceptados = /^[a-zA-Z-\u00f1\u00d1]+((((\s){1})?)[a-zA-Z-\u00f1\u00d1]((([\.\,\\\/]){1})?))*$/ ;
                        if(formulario[i].value.match(valoresAceptados)){
                            // console.log(formulario[i].value);
                            continuar = true;
                            arreglo.push(continuar);
                        }else{
                            continuar = false;
                            arreglo.push(continuar);
                        }
    
                        break;
                
                    case 'usuario':
                        var valoresAceptados = /^([a-zA-Z0-9\u00f1\u00d1\.\_\@]?)*$/ ;
                        if(formulario[i].value.match(valoresAceptados)){
                            // console.log(formulario[i].value);
                            continuar = true;
                            arreglo.push(continuar);
                        }else{
                            continuar = false;
                            arreglo.push(continuar);
                        }
    
                        break;
                
                    case 'habilidades':
                        var valoresAceptados = /^[a-zA-Z-0-9\u00f1\u00d1]+([a-zA-Z-0-9\u00f1\u00d1\s\.\,\:\;\!\-](\s)?)*$/ ;
                        if(formulario[i].value.match(valoresAceptados)){
                            // console.log(formulario[i].value);
                            continuar = true;
                            arreglo.push(continuar);
                        }else{
                            continuar = false;
                            arreglo.push(continuar);
                        }
    
                        break;
                
                    default:
                        break;
                }
                break;
            case 'tel':
                var valoresAceptados =/^(([\+]{1})?)([0-9])+((([\(\)\-\s]{1})?)[0-9](([\(\)]{1})?))*$/;
                if(formulario[i].value.match(valoresAceptados)){
                    continuar = true;
                    arreglo.push(continuar);
                }else{
                    continuar = false;
                    arreglo.push(continuar);
                }
                break;
            case 'password':
                var valoresAceptados =/^[a-zA-Z0-9\u00f1\u00d1\*\!\$\?\/\(\)\#\\]+$/;
                if(formulario[i].value.match(valoresAceptados)){
                    continuar = true;
                    arreglo.push(continuar);

                }else{
                    continuar = false;
                    if(formulario.getAttribute('id') === 'form-editar-admin'){
                        continuar = true;
                    }
                    arreglo.push(continuar);
                }
                break;
        
            case 'submit':
                continuar = true;
                arreglo.push(continuar);
                break;
            case 'checkbox':
                continuar = true;
                arreglo.push(continuar);

                break;
            default:
                break;
        }

    }
    // VERIFICANDO SI EL ARREGLO CONTIENE EL VALOR FALSE //
    var tipoCorrecto = '';
    if(arreglo.includes(false)){
        tipoCorrecto = false;
    }else{
        tipoCorrecto = true;
    }
    
    return tipoCorrecto;
    
}

